.App {
  position: relative;
  text-align: center;
  height: 500px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('background/tone-board-bg.png'); /* ðŸ‘ˆ Arka plan resmi */
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  opacity: 0.25; /* ðŸ‘ˆ Solukluk burada */
  z-index: -1; /* ðŸ‘ˆ Ä°Ã§eriÄŸin arkasÄ±nda kal */
  pointer-events: none; /* ðŸ‘ˆ TÄ±klamayÄ± engellemesin */
}

